<template>
  <div class="task-detail-container">
    <div class="task-main">
      <div class="header">
        <h1 class="title">
          <i class="el-icon-sunny" style="color: #b04365"></i>
          <span>我的一天</span>
        </h1>
        <p class="datetime">{{ '日期过滤器' | current }}</p>
      </div>

      <su-item
        :data="tasks"
        title="content"
        other="listName"
        @comp-toggle="complete"
        @impt-toggle="importToggle"
        @active="handleActive"
      ></su-item>
      <su-input
        placeholder="添加任务"
        :list-prop="taskList"
        @select-lid="handleSelectLid"
        :default-list="defaultList"
        @keyup-enter="handleEnter"
      ></su-input>
    </div>

    <div class="su-detail" v-if="showDetail">
      <h1>title</h1>
      <div>{{ oneTask }}</div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      showDetail: false,
      oneTask: '',
      oldIndex: -1,
      tasks: null,
      // taskList: this.$store.state.taskList,
    };
  },
  created() {
    // 加载任务记录
    this.loadTasks();
  },
  computed: {
    // 从 vuex 获取清单 list
    taskList() {
      return this.$store.state.taskList;
    },
    // 获取默认的清单
    defaultList() {
      return this.$store.state.defaultList;
    }
  },
  methods: {
    complete(index) {
      this.tasks[index].complete = !this.tasks[index].complete;
    },
    importToggle(index) {
      this.tasks[index].import = !this.tasks[index].import;
    },
    handleActive(index) {
      if (index !== -1) {
        this.showDetail = true;
        this.oneTask = this.tasks[index].content;
      } else {
        this.showDetail = false;
      }

      console.log(index);
    },
    handleSelectLid(lid) {
      this.lid = lid;
      console.log(this.lid);
    },
    // 添加记录事件处理函数
    handleEnter(newItem) {
      this.tasks.push(newItem);
    },
    // 获取数据
    async loadTasks() {
      const { data: res } = await this.$http.get('tasks/myday');
      // console.log('enter', res);
      if (res.meta.status !== 200) return this.$message.error('获取数据失败！');
      const mytasks = res.data.myday_tasks, len = mytasks.length;
      for (let i = 0; i < len; i++) {
        if (mytasks[i].end_time === '0000-00-00 00:00:00') {
          mytasks[i].complete = false;
        } else {
          mytasks[i].complete = true;
        }

        if (mytasks[i].import === '0') {
          mytasks[i].import = false;
        } else {
          mytasks[i].import = true;
        }
      }
      this.tasks = mytasks;
      // window.localStorage.setItem('token', res.data.token);
      console.log(this.tasks);
    },
  },
};
</script>

<style lang="less" scoped>
.title {
  color: #586570;
  font-size: 30px;
  span {
    padding-left: 20px;
  }
}
.datetime {
  margin-left: 50px;
  color: #586570;
  font-size: 14px;
}
.task-main {
  display: flex;
  flex: 1;
  flex-direction: column;
  overflow: auto;
  height: 100%;
  position: relative;
  &::-webkit-scrollbar {
    width: 0;
  }
  -ms-overflow-style: none;
  overflow: -moz-scrollbars-none;
  // flex-basis: auto;
  // width: 100%;
}
.header {
  flex-shrink: 0;
}
.su-item {
  flex: 1;
  margin-top: 30px;
  width: 100%;
  overflow: auto;
  -ms-overflow-style: none;
  overflow: -moz-scrollbars-none;
  &::-webkit-scrollbar {
    width: 0;
  }
}
.task-main .su-input {
  width: 100%;
}
.su-detail {
  flex-shrink: 0;
  width: 360px;
  height: 100%; /**container 有1px的边框 因此不用 100vh */
  background: #f5f5f5;
}
</style>